@import url(https://fonts.googleapis.com/css2?family=Roboto);
* {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
   font-family: "Roboto", sans-serif;
   list-style: none;
}

body {
   background-color: #111;
}

.row {
   margin-left: 20px;
   color: white;
   position: relative;
   margin-bottom: 7rem;
}
.row,
.row h2 {
   margin-top: -6rem;
   z-index: 100;
}

.row__posters {
   display: flex;
   overflow-y: hidden;
   overflow-x: scroll;
   padding: 20px;
   transition: all 0.5s;
}

.row__posters::-webkit-scrollbar {
   display: none;
}

.row__poster {
   object-fit: contain;
   width: 100%;
   max-height: 150px;
   transition: transform 0.45s;
   margin-right: 10px;
}

.row__poster:hover {
   transform: scale(1.08);
}

.row__poster__large {
   max-height: 250px;
}

.row__poster__large:hover {
   transform: scale(1.09);
}

.slide__right__large,
.slide__left__large {
   height: 250px;
}

.slide__right,
.slide__left {
   position: absolute;
   height: 150px;
   z-index: 100;
   border: none;
   width: 50px;
   background-color: rgba(0, 0, 0, 0.5);
   color: white;
   font-size: 2rem;
}

.slide__right__large,
.slide__left__large {
   height: 250px;
}

.slide__right:focus,
.slide__left:focus {
   border: none;
   outline: none;
}

.slide__left {
   left: 0;
}

.slide__right {
   right: 0;
}

@media only screen and (max-width: 400px) {
   .row,
   .row h2 {
      margin-top: 0;
   }

   .row {
      margin-bottom: 1rem;
   }

   .slide__left,
   .slide__right {
      display: none;
   }
}

.banner {
   color: white;
   object-fit: contain;
   height: 85vh;
   position: relative;
}

.banner__contents {
   margin-left: 30px;
   padding-top: 400px;
   height: 190px;
   z-index: 10;
   position: relative;
}

.banner__title {
   font-size: 4rem;
   font-weight: 700;
   padding-bottom: 0.3rem;
}

.banner__description {
   width: 45rem;
   line-height: 1.5;
   padding-top: 1rem;
   font-size: 1.1rem;
   max-width: 800px;
   height: 100px;
}

.banner__button {
   cursor: pointer;
   color: white;
   outline: none;
   border: none;
   font-weight: 700;
   font-size: 1rem;
   border-radius: 0.2vw;
   margin-right: 1rem;
   background-color: rgb(51, 51, 51, 0.75);
   padding: 1.1rem 3rem;
}

.banner__button:hover {
   color: black;
   background-color: #e6e6e6;
   transition: all 0.2s;
}

.banner__fadeBottom {
   height: 500px;
   width: 100%;
   bottom: 0;
   position: absolute;
   background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
   );
   z-index: 5;
}

/* Mobile responsiveness */

@media only screen and (max-width: 400px) {
   .banner {
      height: 70vh;
   }

   .banner__contents {
      padding-top: 130px;
   }
   .banner__title {
      font-size: 2rem;
      margin-bottom: 10px;
   }
   .banner__description {
      max-width: 340px;
   }
   .banner__button {
      padding: 10px 30px;
   }
}

nav {
   display: flex;
   justify-content: space-between;
   position: absolute;
   z-index: 120;
   width: 100%;
   height: 100px;
   position: fixed;
   padding: 1rem;
   transition: all 0.5s ease-in;
}

.nav__logo {
   width: 100px;
   margin-left: 3rem;
   cursor: pointer;
}

.nav__avatar {
   height: 55px;
   cursor: pointer;
   margin-right: 30px;
}

.nav__black {
   background-color: #111;
}

/* Mobile responsivness */

@media only screen and (max-width: 400px) {
   nav {
      padding: 0.5rem;
      height: 50px;
   }

   .nav__logo {
      margin-left: 10px;
   }

   .nav__avatar {
      height: 30px;
      margin-right: 0px;
   }
}

